import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Router} from '@angular/router'
// import { Observable } from 'rxjs';
// import {posts} from './auth'



@Injectable({
  providedIn: 'root'
})


export class AuthService {
  private _registerurl="http://localhost:3000/api/register";
  private _loginrurl="http://localhost:3000/api/login";
  
  constructor(private http : HttpClient, private  _routes:Router) { }
 
  registerUser(user){
   return this.http.post<any>(this._registerurl,user)

  }
  loginUser(user){
    return this.http.post<any>(this._loginrurl,user) 
   }

   loggedIn(){
     return !!localStorage.getItem("token")

   }  

   logout(){
     localStorage.removeItem("token")
     this._routes.navigate(['/events'])
   }

   getToken (){
     return localStorage.getItem('token')
   }
}
